<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">ozon商品&系统内商品 配对</div>
      <div class="modal-body">
        <h3>基本信息</h3>
        <div class="row">
          <div class="columns">
            <img :src="form.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">ID:</span>
            <span class="value">{{ form.id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">名称:</span>
            <span class="value">{{ form.name }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">offer_id:</span>
            <span class="value">{{ form.offer_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">sku:</span>
            <span class="value">{{ form.sku }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">product_id:</span>
            <span class="value">{{ form.product_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">fbo佣金百分比:</span>
            <span class="value">{{ form.fbo_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">fbo佣金额:</span>
            <span class="value">{{ form.fbo_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">fbp佣金百分比:</span>
            <span class="value">{{ form.fbp_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">fbp佣金额:</span>
            <span class="value">{{ form.fbp_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">fbs佣金百分比:</span>
            <span class="value">{{ form.fbs_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">fbs佣金额:</span>
            <span class="value">{{ form.fbs_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">rfbs佣金百分比:</span>
            <span class="value">{{ form.rfbs_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">rfbs佣金额:</span>
            <span class="value">{{ form.rfbs_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">货币单位:</span>
            <span class="value">{{ form.currency_code}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">店铺名称:</span>
            <span class="value">{{ form.shop?form.shop.name:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">负责人员:</span>
            <span class="value">{{ form.owner?form.owner.name:"暂无" }}</span>
          </div>
        </div>

        <h3 style="margin-top: 10px;">商品配对</h3>
        <div v-for="(system_product_msg, index_system_product_msg) in system_products_msg" :key="system_product_msg" style="display: flex;">
          <span style="margin-right: 8px; display: flex; ;align-items: center;">{{ index_system_product_msg+1 }}号商品.</span>
          <div>
            <a-input v-model="system_products_msg[index_system_product_msg]['id']" style="width: 300px; margin-right: 10px"/>
            <a-input v-model="system_products_msg[index_system_product_msg]['quantity']" style="width: 100px;"/>
            <a-button type="danger" icon="delete" size="small"  @click="() => delpackage(index_system_product_msg)" style="margin-left: 10px;">删除</a-button>
          </div>  
        </div>
        <a-button icon="edit" size="small" @click="addSystemProduct" style="margin-left: 600px;">新增配对商品</a-button>
    </div>
    </a-modal>
  </div>
</template>

<script>
import moment from 'moment'
import {bindSystemProducts } from '@/api/operation'
import { message } from "ant-design-vue";

export default {
  name: 'MatchFromModal',
  props: ['visible', 'form',"approval_visible"],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      system_products_msg:this.form.system_products?this.form.system_products.map(item => {
        return {id:item.id,quantity:0}
      }):[]
    };
  },
  mounted(){
    
  },
  watch:{
    approval_visible:{
      handler(newValue){
        this.system_products_msg = this.form.system_products?this.form.system_products.map(item => {
        return {id:item.id,quantity:item.quantity}
      }):[]
      },
      deep:true
    },
  },
  computed: {
    in_process_at_time(){
      return moment(this.form.in_process_at).format('YYYY-MM-DD HH:mm:ss')
    },
    shipment_date(){
      return moment(this.form.shipment_date).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  methods: {
    addSystemProduct(){
      this.system_products_msg.push({id:"请输入需要绑定的系统内商品id",quantity:0})
    },
    delpackage(index){
      this.system_products_msg.splice(index,1)
    },
    
    confirm() {
        bindSystemProducts({"ozon_product_id":this.form.id,"system_products_msg":this.system_products_msg}).then(
          res => {
            message.success(res.msg)
            this.system_products_msg = [];
            this.$emit('update', false);
        })
    },
    cancel() {
      this.system_products_msg = []
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns .label{
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}

button {
  margin-right: 2px;
}
</style>